/***** Border-box *****/
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

/***** Global Styles *****/

body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  color: white;
  background: darkslategray;
}

section {
  border-bottom: 4px solid black;
  padding: 30px 0;
}

h1, h2 {
  font-weight: bold;
  text-align: center;
}

h1 {
    margin: 1rem;
}

h2 {
    font-size: 3vw;
    margin-top: 0.5rem;
    margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  body {
    font-size: 2vw;
  }
}

@media (min-width: 900px) {
  body {
    font-size: 18px;
  }
}


@media (max-width: 1200px) {  
    h2 {
      font-size: 36px;
    }
}

@media (min-width: 1600px) {  
  h2 {
    font-size: 48px;
  }
}
.Header_pageHeader__2crJF {
    position: fixed;
    top: 0;
    padding: 5px 0;
    width: 100%;
    background-color: black;
    border-bottom: 2px solid black;
    z-index: 100;
}

.Header_flex__28bO6 {
    width: 80%;
    max-width: 500px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.Header_navLink__2STpr {
    color: skyblue;
    text-decoration: none;
    font-weight: bold;
    font-size: 1.5vw;
    position: relative;
    padding: 3px 0 3px;
    cursor: pointer;
    display: block;
}

.Header_navLink__2STpr:before {
    content: "";
    position: absolute;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: skyblue;
    height: 3px;
    transition-property: left, right;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
}

.Header_navLink__2STpr:hover:before {
    left: 0;
    right: 0;
}

@media (min-width: 1600px) {
    .Header_navLink__2STpr {
        font-size: 24px;
    }
}

@media (max-width: 1333px) {
    .Header_navLink__2STpr {
        font-size: 20px;
    }
}

@media (max-width: 400px) {
    .Header_navLink__2STpr {
        font-size: 16px;
    }
}


@media (max-width: 768px) {
    .Header_navLink__2STpr {
        margin: 10px 0;
    }
}
